.loader{
    position: absolute;
    width: 100%;
    height: calc(100% - 2px);
    top: 0;
    z-index: 12;
    display: flex;
    flex-direction: column;
    justify-content: center;
    vertical-align: middle;
    background: #1D1F39;
}

.loader img{
    max-width: 460px;
    margin: auto;
}